html {
  height: 100%;
  font-size: 100%;
}

body {
  min-height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

body header {
  flex: none;
}

body main {
  flex: 1 0 auto;
}

body footer {
  flex: none;
}

h1, h2, h3, h3, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  padding: 0;
  display: block;
